import helper from '../../common/helper.js';
var _self;
export default {

	data() {
		return {
			rwArray: {},
			rwbzArray: {},
			dateDes: "",
			TaskId: "",
			rwid: '',
			date: '',
			Merimg:[]
		}
	},
	onLoad(e) { //e接收任务的id
		_self = this;
		this.lqrwlist(e);
		this.lqid = e.rwid;
	},
	methods: {
		// 领取任务列表
		lqrwlist(e) {
			var endtime = this.data;
			helper.islogin_Mer(true);
			uni.showLoading({
				title: "加载中....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'mtask/gettask',
				method: "GET",
				data: helper.postdata({
					taskid: e.rwid,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.rwArray = res.data.data.baseinfo;
						this.rwArray.CreateDate = this.rwArray.CreateDate.substr(0, 10);
						this.rwArray.EndDate = this.rwArray.EndDate.substr(0, 10);
						this.Merimg = res.data.data.baseinfo.Pictures.split(';')
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					uni.hideLoading();
				},
				fail: (res) => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请返回重试！'
					});
				}
			})
		},
	}
}
